/* eslint-disable @stylistic/max-len */
/* eslint-disable no-null/no-null */
import React from '@teact'
import {
  memo, useCallback, useEffect, useRef, useState,
} from '../../../lib/teact/teact';
import { withGlobal } from '../../../global';

import { injectComponent } from '../injectComponent';
import buildStyle from '../../../util/buildStyle';
import RoomAIEntryButton from './room-ai-entry-button';

import './room-ai.module.scss';

interface StateProps {
  chatId: string;
}

const RoomAIEntryWrapper = (props: StateProps) => {
  const { chatId } = props;
  // const containerRef = useRef<HTMLDivElement>();
  const [isDragging, setIsDragging] = useState(false);
  const [position, setPosition] = useState({
    x: 0,
    y: 224, // 14rem = 224px (14 * 16)
  });
  const dragStartRef = useRef({
    x: 0,
    y: 0,
    startX: 0,
    startY: 0,
  });
  const hasDraggedRef = useRef(false);
  const isDraggingRef = useRef(false);
  const positionRef = useRef(position);

  // positionref
  useEffect(() => {
    positionRef.current = position;
  }, [position]);

  // isDraggingref
  useEffect(() => {
    isDraggingRef.current = isDragging;
  }, [isDragging]);

  // localStorage
  const loadPosition = useCallback(() => {
    const savedPosition = localStorage.getItem('room-ai-position');
    if (savedPosition) {
      const pos = JSON.parse(savedPosition);
      setPosition(pos);
    } else {
      // 16px224px
      const defaultX = -16; // max(1rem, env(safe-area-inset-right))  16px
      setPosition({ x: defaultX, y: 224 });
    }
  }, []);

  // localStorage
  const savePosition = useCallback((pos: { x: number; y: number }) => {
    localStorage.setItem('room-ai-position', JSON.stringify(pos));
  }, []);

  const longPressTimerRef = useRef<number | null>(null);

  const handleMouseMove = useCallback((e: MouseEvent) => {
    if (!isDragging) return;

    hasDraggedRef.current = true;
    const deltaX = e.clientX - dragStartRef.current.x;
    const deltaY = e.clientY - dragStartRef.current.y;

    const newPosition = {
      x: dragStartRef.current.startX + deltaX,
      y: dragStartRef.current.startY - deltaY, // YY
    };

    // 
    const containerWidth = 62;
    const containerHeight = 62;
    const minX = -window.innerWidth + containerWidth; // 
    const maxX = 0; // 
    const minY = 0; // 
    const maxY = window.innerHeight - containerHeight; // 

    newPosition.x = Math.max(minX, Math.min(maxX, newPosition.x));
    newPosition.y = Math.max(minY, Math.min(maxY, newPosition.y));

    setPosition(newPosition);
  }, [isDragging]);

  const handleMouseUp = useCallback(() => {
    // 
    if (longPressTimerRef.current) {
      clearTimeout(longPressTimerRef.current);
      longPressTimerRef.current = null;
    }

    // 
    if (isDragging) {
      if (hasDraggedRef.current) {
        savePosition(positionRef.current);
      }
    }
    setIsDragging(false);

    // 
    setTimeout(() => {
      hasDraggedRef.current = false;
    }, 100);
  }, [isDragging, savePosition]);

  const handleMouseDown = useCallback((e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();

    hasDraggedRef.current = false;
    dragStartRef.current = {
      x: e.clientX,
      y: e.clientY,
      startX: position.x,
      startY: position.y,
    };

    // 150ms
    longPressTimerRef.current = window.setTimeout(() => {
      setIsDragging(true);
    }, 150);
  }, [position]);

  const handleMouseLeave = useCallback(() => {
    // 
    if (longPressTimerRef.current) {
      clearTimeout(longPressTimerRef.current);
      longPressTimerRef.current = null;
    }
  }, []);

  const handleClick = useCallback((e: React.MouseEvent) => {
    // 
    if (hasDraggedRef.current || isDragging) {
      e.preventDefault();
      e.stopPropagation();
    }
  }, [isDragging]);

  // 
  useEffect(() => {
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };
  }, [isDragging, handleMouseMove, handleMouseUp]);

  useEffect(() => {
    loadPosition();
  }, [loadPosition]);
  const containerRef = injectComponent({
    component: RoomAIEntryButton,
    props: {
      chatId,
    },
  });

  return (
    <div
      className="room-ai-entry-wrapper"
      ref={containerRef}
      data-dragging={isDragging}
      style={buildStyle(`right: ${-position.x}px; bottom: ${position.y}px; cursor: ${isDragging ? 'grabbing' : 'grab'}; userSelect: none`)}
      onMouseDown={handleMouseDown}
      onMouseLeave={handleMouseLeave}
      onClick={handleClick}
    />
  );
};

export default memo(withGlobal(
  (global, { chatId }): StateProps => {
    return {
      chatId,
    };
  },
)(RoomAIEntryWrapper));
